import { Injectable } from '@angular/core';
import {
    HttpRequest,
    HttpHandler,
    HttpEvent,
    HttpResponse,
    HttpErrorResponse,
    HttpInterceptor
} from '@angular/common/http';
import { Router } from "@angular/router";
import { map, tap } from 'rxjs/operators';
import { NgxSpinnerService } from 'ngx-spinner';
import { Observable } from "rxjs";
import { ToastrService } from 'ngx-toastr';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
    constructor(
        public router: Router,
        private spinner: NgxSpinnerService,
        private toastr : ToastrService
    ) {

    }
    intercept(
        req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        this.spinner.show();
        if (localStorage.getItem('loginData')) {
            let loginData = JSON.parse(localStorage.getItem('loginData'));
            req = req.clone({
                setHeaders: {
                    Authorization: "Token " + loginData.token
                }
            });
        }

        return next.handle(req).pipe(tap(
            (event: HttpEvent<any>) => {
                if (event instanceof HttpResponse) {
                    this.spinner.hide();
                }
            },
            (err: any) => {
                // console.log("err.error.status", err.error);
                if (err.error.status_code == 401) {
                    this.spinner.hide();
                    localStorage.removeItem('loginData');
                    localStorage.removeItem('permissions');
                    this.router.navigate(['/login']);
                    this.toastr.clear();
                    this.toastr.error('', "Your session has expired. Please relogin")
                }
                if (err instanceof HttpErrorResponse) {
                    this.spinner.hide();
                }
            }
        ));
    }

}
