import { Component, OnInit, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Validations } from '../../shared/validators/Validations';
import { environment } from '../../../environments/environment';
import { ToastrService } from 'ngx-toastr';
import { LoginService } from '../../shared/services/login.service';
import { Route } from '@angular/compiler/src/core';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-dashboard',
  templateUrl: 'login.component.html'
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  submitted: boolean = false;

  constructor(
    private titleService: Title,
    private toastr: ToastrService,
    private loginService: LoginService,
    private router: Router,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit() {
    this.titleService.setTitle("Login");
    this.loginForm = new FormGroup({
      email: new FormControl('', [
      	Validators.required, 
      	Validators.pattern(Validations.email_pattern)
      ]),
      password: new FormControl('', [
      	Validators.required, 
      	Validators.minLength(6)
      ])
    });

    let loginData = JSON.parse(localStorage.getItem('loginData'));
    if (loginData) {
      this.router.navigate(['dashboard']);
    }
  }

  submitLoginForm(){
    this.submitted = true;
    if(this.loginForm.valid){
      var formData = new FormData();
      formData.append("email", this.loginForm.controls.email.value);
      formData.append("password", this.loginForm.controls.password.value);
      
      this.loginService.login(formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));
        this.spinner.show();

        if(resData.status_code == 200){
          let loginData = {
            id: resData.data.id,
            email: resData.data.email,
            user_type: resData.data.user_type,
            token : resData.data.token  
          }
          localStorage.setItem('loginData', JSON.stringify(loginData));
          this.router.navigate(['dashboard']).then(() => {
            this.toastr.success('', resData.message);
          });
          this.spinner.hide();
        }else{
          this.spinner.hide();
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
          this.spinner.hide();
        });
    }
  }
}
